<template>
  <div>
    <v-menu offset-y
            transition="slide-x-reverse-transition"
            :close-on-content-click='false'
            nudge-bottom='12'>
      <template v-slot:activator="{ on }">
        <!-- <v-icon v-on="on">mdi-format-list-bulleted-type</v-icon> -->
        <v-btn color="#f2f2f2"
               rounded
               small
               fab
               :elevation='0'
               dark
               v-on="on">
          <span class="black--text">MK</span>
        </v-btn>
      </template>
      <!-- <v-list dense> -->
      <!-- <v-list-item @click="login_out"> -->
      <!-- <v-list-item-title>登出</v-list-item-title> -->
      <div class="logBox">
        <!-- $vuetify.theme.defaults.light.primary -->
        <div class="logTop"
             :style="{backgroundColor: '#739ce8'}">
        </div>
        <div class="logFooter">
          <span class="userhead">
            <img src="https://picsum.photos/200"
                 style="width: 100%;height:100%;border-radius: 50%">
          </span>
          <h2 style="margin-top:88px">{{$store.state.userData.old_pwd}}</h2>
          <v-btn color="primary"
                 rounded
                 class="mt-4 text-capitalize"
                 :elevation='0'
                 min-width='182'
                 min-height='36'
                 @click="changePassword"
                 dark>
            <span>Change Password</span>
          </v-btn>
          <v-spacer style="margin-top: 67px;height: 1px;width:95%;background-color: #e8e8e8;"></v-spacer>
          <div style="height: 52px;"
               class="d-flex align-center">
            <v-btn text
                   rounded
                   class="logout text-capitalize"
                   :style="{color: $vuetify.theme.defaults.light.primary}"
                   @click="login_out">logout</v-btn>
          </div>
        </div>
      </div>
      <!-- </v-list-item> -->
      <!-- </v-list> -->
    </v-menu>
    <mrc_ess_myDialog v-model="changePasswordDialog"
                      title="Reset Password"
                      width='680'
                      @success='savePassword'
                      successText='Submit'
                      cancelText='Cancel'
                      :loading='loading'
                      @cancel='cancel'>
      <mrc_ess_input placeholder="Original Password*"
                     label="Original Password*"
                     background-color='#fff'
                     style="margin-top: 34px"
                     @input="$v.params.old_pwd.$touch()"
                     @blur="$v.params.old_pwd.$touch()"
                     v-model.trim="params.old_pwd"
                     :type='type'
                     :append-icon="icon"
                     @click:append="showPassword"
                     :error-messages="$errors('params','old_pwd',requiredRule.concat([{rule: 'isChina', msg: '不能输入中文'}]))"></mrc_ess_input>
      <mrc_ess_input placeholder="new password*"
                     label="new password*"
                     background-color='#fff'
                     @input="$v.params.user_pwd.$touch()"
                     @blur="$v.params.user_pwd.$touch()"
                     v-model.trim="params.user_pwd"
                     :type='type'
                     :append-icon="icon"
                     @click:append="showPassword"
                     :error-messages="$errors('params','user_pwd',
                     requiredRule.concat(
                     {rule: 'isChina', msg: '不能输入中文'},
                     {rule: 'passwordStrengthRegxRule', msg: regxMsg}))"></mrc_ess_input>
      <mrc_ess_input placeholder="Retype password*"
                     label="Retype password*"
                     background-color='#fff'
                     @input="$v.params.RetypePwd.$touch()"
                     @blur="$v.params.RetypePwd.$touch()"
                     :append-icon="icon"
                     :type='type'
                     @click:append="showPassword"
                     v-model.trim="params.RetypePwd"
                     :error-messages="$errors('params','RetypePwd',requiredRule.concat([{rule: 'sameAsNewPassword',msg: '两次密码输入不一致'}]))"></mrc_ess_input>

    </mrc_ess_myDialog>
  </div>
</template>

<script>
import { required, minLength } from 'vuelidate/lib/validators'
import { updatePassword, passwordStrength } from '@/service/api/forgot-password'
export default {
  components: {},
  props: {},
  data () {
    return {
      loading: false,
      changePasswordDialog: false,
      params: {
        old_pwd: '', // 旧密码
        user_pwd: '', // 新密码
        RetypePwd: '' // 再次输入新密码
      },
      icon: 'mdi-eye-off',
      type: 'password',
      is_showPassword: false,
      regxRule: null,
      regxMsg: ''
    };
  },
  validations: {
    params: {
      old_pwd: {
        // illegal (val) {
        //   return this.illegal(val)
        // },
        required,
        isChina (val) {
          return this.isChina(val)
        }
      },
      user_pwd: {
        required,
        isChina (val) {
          return this.isChina(val)
        },
        passwordStrengthRegxRule (val) {
          return new RegExp(this.regxRule).test(val)
        }
      },
      RetypePwd: {
        required,
        sameAsNewPassword (val) {
          return val && val == this.params.user_pwd
        }
      }
    }
  },
  computed: {},
  created () { },
  watch: {},
  mounted () { },
  beforeRouteUpdate () { },
  methods: {
    login_out () {
      this.$emit('login_out')
    },
    async changePassword () {
      this.$v.params.$reset()
      const res = await this.getPasswordStrength()

      this.changePasswordDialog = true
    },
    async getPasswordStrength () {
      const res = await passwordStrength({ user_account: this.$store.state.userData.user_account })
      if (res.code == 200) {
        this.regxMsg = res.data.ceckmean
        this.regxRule = res.data.cecknum
      }
    },
    async savePassword () {
      this.$v.params.$touch()
      if (this.$v.params.$error) return
      this.loading = true
      const res = await updatePassword({
        old_pwd: this.params.old_pwd,
        user_account: this.$store.state.userData.user_account,
        user_pwd: this.params.user_pwd
      })
      if (res.code == 200) {
        this.$Message({ text: this.$t('message_The_modification_was_successful'), color: 'rgb(0,0,0,0.78)' })
        this.changePasswordDialog = false
        this.$router.push({
          path: '/login'
        })
        this.$Message({ text: this.$t('请重新登录'), color: 'rgb(0,0,0,0.78)' })
      }
      this.loading = false
    },
    cancel () {

    },
    showPassword () {
      this.is_showPassword = !this.is_showPassword
      if (this.is_showPassword) {
        this.icon = 'mdi-eye'
        this.type = 'text'
      } else {
        this.icon = 'mdi-eye-off'
        this.type = 'password'
      }
    },
  },
};
</script>

<style scoped>
.logBox {
  width: 314px;
  height: 378px;
  background-color: #ffffff;
  border-radius: 10px;
  position: relative;
}
.logTop {
  height: 72px;
}
.logFooter {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.userhead {
  display: inline-block;
  width: 96px;
  height: 96px;
  border-radius: 50%;
  background-color: #efefef;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 24px;
}
.logout {
  color: #0f46c1;
  font-family: Noto Sans CJK TC;
  font-weight: medium;
  font-size: 14px;
  line-height: 1.25;
  letter-spacing: 1.25px;
  text-align: center;
}
</style>
